.notfound-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: black;
  text-align: center;
  padding: 20px;
}

.oops-emoji {
  font-size: 100px;
  color: #666;
  margin-bottom: 20px;
}


.notfound-container h1 {
  font-size: 5rem;
  font-weight: bold;
  color: white;
  margin: 10px 0;
}

.notfound-container h2 {
  font-size: 1.5rem;
  color: whitesmoke;
  margin-bottom: 10px;
}

.notfound-container p {
  color: #aaa;
  font-size: 0.95rem;
  line-height: 1.5;
  max-width: 500px;
}

.notfound-container a {
  color: #240b5f;
  text-decoration: none;
  font-weight: 500;
}

.notfound-container a:hover {
  text-decoration: underline;
}
