@import url('https://fonts.googleapis.com/css?family=Sofia&display=swap');
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: black;


  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}
.navbar.logo{

  font-size: 24px;

  font-family: 'Sofia', sans-serif;
  font-weight: 700;
}
.nav-links {
  list-style: none;
  display: flex;
  gap: 15px;
}

.nav-links li a {
  color: white;
  text-decoration: none;
font-size: 16.571px;
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: 300;
  transition: background 0.3s;
  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.nav-links li a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.nav-links li a.active {
  
  color: rgb(5, 19, 149);
  font-weight: bold;
}
