/* Full-page dark background */
body {
  margin: 0;
  padding: 0;
  background-color: #000;
  font-family: 'Poppins', sans-serif;
  color: #f0f0f0;
}

/* Wrapper centers content vertically with padding */
.terms-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 50px 20px;
}

/* Card-style container */
.terms-container {
  max-width: 900px;
  background: #121212;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 10px 35px rgba(0,0,0,0.7);
  animation: fadeInUp 0.5s ease;
}

/* Title styles */
.terms-container h1 {
  font-size: 2.2rem;
  margin-bottom: 10px;
  text-align: center;
  color: #ffffff;
}

.last-updated {
  text-align: center;
  font-size: 0.9rem;
  color: #bbb;
  margin-bottom: 30px;
}

/* Section styling */
.terms-container section {
  margin-bottom: 36px;
}

.terms-container h2 {
  font-size: 1.5rem;
  margin-bottom: 14px;
  color: #ffcc70;
}

.terms-container p,
.terms-container li {
  font-size: 1rem;
  line-height: 1.7;
  color: #e0e0e0;
}

.terms-container ul {
  padding-left: 20px;
  list-style: disc;
}

/* Link style */
.terms-container a {
  color: #4db8ff;
  text-decoration: none;
}

.terms-container a:hover {
  text-decoration: underline;
}

/* Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .terms-container {
    padding: 28px;
  }
  .terms-container h1 {
    font-size: 1.8rem;
  }
  .terms-container h2 {
    font-size: 1.3rem;
  }
}
