/* --- General Footer Styles --- */
.footer {
  padding: 40px 20px;
  background: black;
  color: #555;
  font-family: Arial, sans-serif;
}

.footer-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-bottom: 30px;
}

/* --- Email Input + Button --- */
.footer-input {
  padding: 10px 15px;
  border: 1px solid #ccc;
  outline: none;
  border-radius: 4px 0 0 4px;
}

.footer-btn {
  padding: 11px 20px;
  border: none;
   
      background: linear-gradient(135deg, #051aa3, #010307);
  cursor: pointer;
  border-radius: 0 4px 4px 0;
  font-size: 16px;
  transition: background 0.3s ease;
}

.footer-btn:hover {
  background: #bbb;
}

/* --- Top Navigation Links --- */
.footer-nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 20px;
  padding: 0;
  margin: 0;
}

.footer-nav li a {
  text-decoration: none;
  color: white;
  font-size: 15px;
  transition: color 0.3s ease, border-bottom 0.3s ease;
}

.footer-nav li a:hover {
  color: #a98c8c;
}

/* Highlight for active NavLink */
.footer-nav li .active {
  color: #3b82f6; /* Blue active link */
  border-bottom: 2px solid #3b82f6;
  padding-bottom: 2px;
}

/* --- Social Icons --- */
.footer-social a {
  display: inline-block;
  margin: 0 6px;
  font-size: 18px;
  color: white;
  transition: color 0.3s ease;
}

.footer-social a:hover {
  color: #766161;
}

/* --- Bottom Section --- */
.footer-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
  border-top: 1px solid #eee;
  padding-top: 20px;
}

.footer-links {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.footer-links a {
  text-decoration: none;
  color: white;
  transition: color 0.3s ease, border-bottom 0.3s ease;
}

.footer-links a:hover {
  color: #000;
}

.footer-links .active {
  color: #3b82f6;
  border-bottom: 2px solid #3b82f6;
  padding-bottom: 2px;
}

/* --- Logo and Copy --- */
.footer-logo {
  color: #3b82f6;
  font-weight: bold;
  font-size: 20px;
  margin: 0;
}

.footer-copy {
  color: white;
  font-size: 14px;
  margin: 0;
}

/* --- Responsive --- */
@media (max-width: 768px) {
  .footer-top,
  .footer-bottom {
    flex-direction: column;
    text-align: center;
  }

  .footer-nav {
    justify-content: center;
  }

  .footer-links {
    justify-content: center;
  }
}
